//
// banner
// ----------------------------------------------------------------


.site-banner {
  margin: 0 1em;
  padding: 1em 0;
  @include media-breakpoint-up(medium) {
    margin: 0 1.5em;
    padding: 1.5em 0 1.2em;
  }

  p {
    margin-bottom: 0;
  }

  .site-title {
    font-size: 1.5em;
    margin-bottom: .2em;
    line-height: 1.3;
    a {
      text-decoration: none;
      border-bottom: .1em solid #fff;
    }
  }

  .logo {
    text-align: left;
    img {
      max-width:100%;
      height: auto;
    }
  }
}


