//
// footer
// ----------------------------------------------------------------

.site-footer {
  min-height: 10em;

  a,
  a:link,
  a:visited,
  a:hover,
  a:focus {
    text-decoration: none;
    color: #fff;

    img {
      margin-left: .25em;
    }
  }

  a:hover {
    text-decoration: underline;
  }


  .footer-links {
    background-color: $color-primary;
    color: #fff;
    padding: .4em 0;

    .container {
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include media-breakpoint-up(small) {
        flex-direction: row;
      }
    }

    ul {
      margin: 0 auto;
      text-align: center;
      list-style-type: none;
    }

    li {
      display: inline;

      a {
        color: #fff;
        font-size: 0.875em;
        padding:0 1em;

        @include media-breakpoint-up(small) {
          border-right: 1px solid #fff;
        }
      }

      &:last-child a {
        border: none;
      }
    }
  }

  .big-footer {
    padding: 1em;

    background-color: $gray-2;
    color: #fff;

    @include media-breakpoint-up(medium) {
      display: flex;
      .big-footer-1 {
        flex-grow: 5;
      }
      .big-footer-2 {
        flex-grow: 1;
        flex-basis: 40em;
      }
    }
  }
}
