table {@include table();}


.article-aside {
  padding: 1em 1em;
}

.aside-toc {
  background-color: $gray-9;
  padding: 1em 1em;
  margin-bottom: 1em;

  p {
    margin-bottom: .5em;
    font-weight: bold;
  }

  ul {
    padding:0;
    margin: 0em;
    li {
      margin: .25em 0;
      list-style-type: none;
      li {
        list-style-type: disc;
        margin-left: 1em;
      }
    }
  }
}



