/*! layout */


body {
  @include rwd-typo-size(body);
  background-color: $gray-10;
    padding: .5em;
  @include media-breakpoint-up(medium) {
    padding: 1em;
  }
}

.container {
  @include rwd-block-width(container);
  padding: 0;
}

.main-container.main-container {
  // padding-top: 3em;
  // padding-bottom: 3em;
  background: #fff;
  padding: 0;
}

.site-main {
  padding-top: 1.5em;
  min-height: calc(100vh - 25em);
}

.article-header {
  padding: 0 1.5em;
}
.article {
  p + ul {
    margin-top: -.7em;
  }
}

.article-aside {
  padding-top: 4em;
}

.one-column {
  max-width: 48em;
  margin: 0 auto;
  background-color: #fff;
  // padding: 1em;

}

.two-columns {

  .article {
    padding: 1em 1em;
  }


  @include media-breakpoint-up(medium) {
    display: flex;
    justify-content: space-around;

    .article-header {
      // flex:5;
      padding: 1em 1.5em;
    }

    .article {
      flex:5;
      padding: 1em 1.5em;
    }

    .article-aside {
      // float: right;
      // order:3;
      order:2;
      flex:3;
    }
  }
}

.contribute {}
